.contenido{
    width: 300px;
    height: 300px;
    display: flex;
}

.caratula{
    width: 150px;
    height: 150px;
    background-image: url(https://www.todorock.com/wp-content/uploads/2019/10/linkin-park-hybrid-theory.jpeg);
    background-size: cover;
}

.disco{
    width: 150px;
    height: 150px;
    border-radius: 50%;
    background-color: black;
    position: absolute;
    z-index: -1;
    transition: all .5s linear;
    display: flex;
    justify-content: center;
    align-items: center;
}

.centro{
    background-color: white;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    border:3px solid red;
}


.contenido:hover .disco{
    transform: translateX(100px) rotate(90deg);
}